<template>
    <Header :homeDisplay="homeDisplay" />
    <div class="tablepick">
        <form @submit.prevent="handleSubmit">

            <div class="d-flex justify-content-end">                    
                <a>식사선택   : </a>           
                <select v-model="op_selected">
                    <option v-for="option in options" :key="option.value" :value="option.value">
                        {{ option.text }}
                    </option>
                </select>                    
            </div>
            <div class="mycal">
                <div class="justify-content-center">
                    <span v-if="op_selected==='one'">
                        <VDatePicker :attributes='attributes' @dayclick="onDayClick" mode="multiple" 
                        :disabled-dates="disabledDates" :color="selectedColor" :min-date='new Date()' 
                        :disabled-attribute='disabledAttribute' class="my-calendar" />        
                    </span>
                    <span v-if="op_selected==='range'">
                        <VDatePicker :attributes='attributes' @dayclick="onRangeClick" range 
                        :disabled-dates="disabledDates" :color="selectedColor" :min-date='new Date()' 
                        :disabled-attribute='disabledAttribute' class="my-calendar" />            
                    </span>  
                </div>
            </div>

            <div class="mb-3 border bg-warning border-warning rounded p-2" style="--bs-bg-opacity: 0.2" v-if="!!this.message">
                {{ this.message }}
            </div>

            <div class="footer">
                <div class="d-flex justify-content-center">
                    <button type="submit" class="btn btn-primary" size="lg" >확 정</button>
                </div>
            </div>
        </form>
    </div>    
    <!-- <div v-for="ticket in this.getTicketList" :key="ticket.id"> {{ ticket.id }} {{ ticket.date }}</div>                 -->

</template>

<script>
import {ref} from 'vue';
import Header from "@/components/Header.vue";

const op_selected = ref('one');
const index = ref('0');
var startday = ref();
var endday = ref();
var days= ref([]);
var holiday= ref([]);
const selectedColor = ref('white');

const options = ref([
    { text:'하루씩', value: 'one'},
    { text:'범위지정', value: 'range'},
])

const disabledDates = ref([{
        repeat: {
            weekdays: [1, 7],
        },
}]);

const disabledAttribute = ref([{
        contentStyle: {
            color: 'red',
        },
        contentHoverStyle: {
                cursor: 'default',
                backgroundColor: 'transparent',
        },
}]);

const homeDisplay = ref(false);
const meales = ref([]);

export default {
    name: "HomeView",
    components: { 
        Header,
    },
    data() {
            return {
                days: days,
                holiday:holiday,
                twin_days:[],
                op_selected: op_selected,
                options: options,
                date: new Date(),
                disabledDates,
                selectedColor,
                disabledAttribute,
                newTicket: {
                    id: "",
                    date: "",
                    uid: "",
                    created_at:0,
                },
                // todos,
                message:"",
                timer: 1,
                interval: "",
                homeDisplay: homeDisplay,
                meales: meales,
            }
    },
    computed: {        
        getTicketList() {
            return this.$store.state.tickets.ticketList;            
        },        
        dates() {
            return this.days.map(day => day.date);
        },
        getHolidayList() {
            return this.$store.state.holiday.holidayList;            
        },
        holidates() {
            return this.holiday.map(day => day.date);
        },
        fetchMealdayList() {
            return this.$store.state.meal.mealList;            
        }, 
        attributes() {
            return [
                {
                    contentStyle: {
                        fontWeight: '700',
                        fontSize: '.9rem',
                    },
                    dates: new Date(),
                },

                ...this.dates.map(date => ({
                    highlight: 'blue',
                    dates: date,
                })),
                
                ...this.holidates.map(date => ({
                    highlight: 'red',
                    dates: date,
                })),

                ...this.meales,
            ];
        },               
    },
    watch:{
        getTicketList(val){
            this.days = val;            
            let tdays=[];
            val.forEach ( (day) => {
                tdays.push(day);
            })
            this.twin_days = tdays;
        },
        getHolidayList(val){
            if (val.length>0) {
                this.holiday = val;
            }            
        },
        fetchMealdayList(val) {
            this.meales=[];
            if(val.length>0) {
                val.forEach ( (meal) => {
                    this.meales.push({
                        key: meal.id,
                        dates: meal.date,
                        dot: 'yellow',
                        popover: {
                            label: meal.meal,
                        },
                    });
                });
            }
        }
    },
    mounted() {
        this.$store.dispatch("fetchTicketList");
        this.$store.dispatch("fetchHolidayList");
        this.$store.dispatch("fetchMealdayList");
    },    
    methods: {
            onDayClick(day) {
                const now = new Date();
                const offset = new Date().getTimezoneOffset() * 60000
                const date = new Date(day.date - offset);                
                let day_s = date.toISOString().split('T')[0];
                let nowday_s = now.toISOString().split('T')[0];
                if( day_s >= nowday_s) {
                    const ix = this.holiday.findIndex(d => d.id === day.id);
                    if (ix < 0) {
                        const idx = this.days.findIndex(d => d.id === day.id);                                        
                        if (idx >= 0) {
                            this.days.splice(idx, 1);
                        } else {
                            // const offset = new Date().getTimezoneOffset() * 60000
                            // const date = new Date(day.date - offset);

                            let weekend = this.dateDisabled(date);                    
                            if (!weekend) {
                                this.days.push({
                                    id: day.id,
                                    date: date.toISOString(),
                                    uid:this.$store.state.auth.user.uid,
                                    created_at:Date.now(),
                                });                     
                            }                    
                        }
                    }                    
                }
            },

            onRangeClick(day) {
                if (index.value == 0 ) {
                    index.value=1;
                    startday = day.date;
                } else {
                    endday = day.date;
                    this.getDateRange(startday, endday);
                }            
            },

            getDateRange(startDate, endDate) {
                let start = new Date(startDate);
                let end = new Date(endDate);
                if (start > end ) {
                    var tmp = start;
                    start = end;
                    end = tmp;
                }
                while (start <= end) {
                    const offset = new Date().getTimezoneOffset() * 60000
                    const dstart = new Date(start - offset);                
                    var day = {
                        id  : dstart.toISOString().split('T')[0],
                        date: dstart.toISOString(),
                    };

                    const ix = this.holiday.findIndex(d => d.id === day.id);
                    if (ix < 0) {
                        const idx = this.days.findIndex(d => d.id === day.id);
                        if (idx === -1) {
                            const mydate = new Date(day.date);
                            let weekend = this.dateDisabled(mydate);                    
                            if (!weekend) {
                                this.days.push({
                                    id: day.id,
                                    date: day.date,
                                    uid:this.$store.state.auth.user.uid,
                                    created_at:Date.now(),
                                });
                            }                    
                        }
                    }
                    start.setDate(start.getDate() + 1);
                }            
            },

            async handleSubmit() {                
                let tmpdays = [];                
                this.days.forEach( (day) => {
                    const mydate = new Date(day.date);
                    let weekend = this.dateDisabled(mydate);
                    if (!weekend) {                         
                        tmpdays.push(day);
                    }                    
                });                
                tmpdays.forEach( (day) =>{
                    if ( this.twin_days.indexOf(day) ===-1) {                        
                        this.$store.dispatch("addNewTicket", day);                        
                    }
                });
                this.days = tmpdays;
                this.twin_days.forEach( (day)=>{
                    if (this.days.indexOf(day) === -1){
                        // console.log('please delete from firebase');
                        this.$store.dispatch("deleteTicket", day.id);
                    }
                });                
                this.message="식수 변경이 완료 되었습니다";            
                this.startTimer();
            },
            
            dateDisabled(date) {                
                const weekday = date.getDay()
                return weekday === 0 || weekday === 6
            },

            startTimer() {
                this.interval = setInterval(() => {
                    if (this.timer === 0) {
                        clearInterval(this.interval)
                        this.message="";                
                    } else {
                        this.timer--
                    }             
                }, 100)
            }
        }
};
</script>

<style>
.my-calendar .vc-weekday-1, .my-calendar .vc-weekday-7 {
    color: #f50303;
}

.my-calendar.vc-container {
    --day-border: 1px solid #b8c2cc;
    --day-border-highlight: 1px solid #b8c2cc;
    --day-width: 90px;
    --day-height: 90px;
    --weekday-bg: #f8fafc;
    --weekday-border: 1px solid #eaeaea;

    border-radius: 5%;
    width: 98%;
}
.tablepick {
    padding: 4%;
}
.mycal{
    padding: 10px;
}
.footer{
    padding: 20px;
}

</style>