<template>
    <div class="fheader">
        <b-card title="Card Title" no-body>
            <b-card-header header-tag="nav">
                <b-nav align="center">
                    <b-button pill size="sm" variant="success" to="/userinfo" v-if="this.getAuth===false">사용자정보</b-button>
                    <b-button pill size="sm" variant="success" to="/ticketcount" v-if="this.getAuth===true && !homeDisplay">당일식수</b-button>
                    <b-button pill size="sm" variant="success" to="/useradmin" v-if="this.getAuth===true && !homeDisplay">권한설정</b-button>
                    <b-button pill size="sm" variant="success" to="/holiday" v-if="this.getAuth===true && !homeDisplay">휴일설정</b-button>
                    <b-button pill size="sm" variant="success" to="/menu" v-if="this.getAuth===true  && !homeDisplay">메뉴입력</b-button>
                    <b-button pill size="sm" variant="success" to="/home" v-if="homeDisplay">홈</b-button>
                    <b-button size="sm" variant="danger" @click="this.signOut">로그아웃</b-button>
                </b-nav>
            </b-card-header>
            <b-card-body v-if="this.getAuth===true">
                <b-card-text> 모든 권한 관리자인 [ {{this.getUserName}} ]님 환영합니다.</b-card-text>
            </b-card-body>
            <b-card-body v-if="this.getAuth===false">
                <b-card-text> {{this.getUserName}}님 환영합니다. </b-card-text>
            </b-card-body>

        </b-card>
            
        <!-- <b-nav align="center">
            <b-nav-text> {{this.getUserName}}님 환영합니다. </b-nav-text>
            <b-button size="sm" variant="danger" @click="this.signOut">로그아웃</b-button>
            <b-button pill size="sm" variant="success" to="/ticketcount" v-if="this.getAuth===true">당일식수</b-button>
            <b-button pill size="sm" variant="success" to="/userinfo" v-if="this.getAuth===true">사용자설정</b-button>
            <b-nav-text>계정 : {{this.getEmail}}</b-nav-text>
            <b-nav-item to="/ticketcount" v-if="this.getAuth>=1">당일식수</b-nav-item>
            <b-button @click="this.signOut" size="sm" variant="danger">로그아웃</b-button>            
        </b-nav>         -->
    </div>    
</template>

<script>

export default {
    computed: {
        // getPhoto() {
        // return this.$store.state.auth.user.photoURL;
        // },
        getAuth() {
            return this.$store.state.auth.user.authority;
        },
        getUserName() {
        return this.$store.state.auth.user.username;
        },
        getEmail() {
        return this.$store.state.auth.user.email;
        },
    },
    methods: {
        signOut() {
        this.$store.dispatch("signOut");
        },
    },
    props:{
        homeDisplay: Boolean,
    }    
};
</script>

<style scoped>
.fheader {
    padding: 10px;
}
</style>